<template>
  <span class="numbers_box" :data-id="datas.index">
    <span class="decrease noselect" title="减少" @click="decrease" unselectable="on">-</span>
    <input class="number_put" type="number" v-model="num"/>
    <span class="add noselect" title="添加" @click="add" unselectable="on">+</span>
  </span>
</template>

<script>
  export default {
    name: 'num',
    props: ['datas'],
    data () {
      return {
        num: this.datas.num,
        editing: 0
      }
    },
    watch: {
      numbers: {
        handler () {
          this.num = this.numbers
        },
        deep: true
      },
      num: {
        handler () {
          if (this.num === '' || isNaN(this.num)) {
            this.num = 1
          }
          this.editing_done()
        },
        deep: true
      }
    },
    computed: {
      numbers () {
        return this.datas.num
      }
    },
    methods: {
      decrease () {
        this.$store.commit('decrease', {
          index: this.datas.index
        })
      },
      add () {
        this.$store.commit('add', {
          index: this.datas.index
        })
      },
      editing_done () {
        this.editing = 0
        this.$store.commit('change_num', {
          index: this.datas.index,
          num: this.num
        })
      }
    },
    directives: {
      'focus': {
        update (el, binding) {
          if (binding.value) {
            el.focus()
          }
        }
      }
    }
  }
</script>

<style>
</style>


