import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex)
/* eslint indent: "error" */
/* eslint quotes: ["error", "double"] */
let goodsDetail = [
  {
    "describe": "3D Touch、1200万像素照片、4k视频，强大功能于一身。",
    "pictures": {
      "silver": "../../static/img/iphone6s-silver-select-2015.png",
      "deep_grey": "../../static/img/iphone6s-gray-select-2015.png",
      "golden": "../../static/img/iphone6s-gold-select-2015.png",
      "rose_gold": "../../static/img/iphone6s-rosegold-select-2015.png"
    },
    "name": "Apple/苹果 iPhone 6S",
    "minPrice": 5288,
    "maxPrice": 6888,
    "color": {
      "silver": "银色",
      "deep_grey": "深空灰色",
      "golden": "金色",
      "rose_gold": "玫瑰金色"
    },
    "storages": {
      "sixteen": "16GB",
      "sixtyFour": "64GB",
      "one_hundred_and_twenty_eight": "128GB"
    },
    "unit_price": 0,
    "pay_number": 1,
    "subtotal": 0
  }
]

var saveAndGet = {
  save (key, value) {
    localStorage.setItem(key, JSON.stringify(value))
  },
  fetch (key) {
    return JSON.parse(localStorage.getItem(key)) || []
  }
}

var store = new Vuex.Store({
  state: {
    shoppingCartList: saveAndGet.fetch("data"),
    goodsDetail,
    choose_data: {
      "choose_phone_pic": goodsDetail[0].pictures.silver,
      "choose_color": "银色",
      "choose_storage": "",
      "choose_color_index": "silver",
      "choose_storage_index": "",
      "choose_price": 0
    },
    defaultChooses: ["silver", "null"]
  },
  mutations: {
    check_all (state, payLoad) {
      if (!payLoad.check_state) {
        for (var i = 0; i < state.shoppingCartList.length; i++) {
          if (!state.shoppingCartList[i].checked) {
            state.shoppingCartList[i].checked = true
          }
        }
      } else {
        for (var j = 0; j < state.shoppingCartList.length; j++) {
          state.shoppingCartList[j].checked = false
        }
      }
    },
    choose (state, payLoad) {
      var oldState = state.shoppingCartList[payLoad.index].checked
      state.shoppingCartList[payLoad.index].checked = !oldState
    },
    decrease (state, payLoad) {
      var datas = state.shoppingCartList[payLoad.index]
      if (datas.pay_number > 1) {
        datas.pay_number -= 1
        datas.subtotal = datas.pay_number * datas.unit_price
      }
    },
    add (state, payLoad) {
      var datas = state.shoppingCartList[payLoad.index]
      datas.pay_number += 1
      datas.subtotal = datas.pay_number * datas.unit_price
    },
    change_num (state, payLoad) {
      var datas = state.shoppingCartList[payLoad.index]
      datas.pay_number = parseInt(payLoad.num)
      datas.subtotal = datas.pay_number * datas.unit_price
    },
    del (state, index) {
      state.shoppingCartList.splice(index, 1)
    },
    add_shopping_cart (state, payLoad) {
      state.shoppingCartList.push({
        checked: true,
        picture: payLoad.pic,
        name: payLoad.name,
        color: payLoad.color,
        type: payLoad.storage,
        unit_price: payLoad.price,
        pay_number: 1,
        subtotal: payLoad.price
      })
      saveAndGet.save("data", state.shoppingCartList)
    },
    change_choose (state, payLoad) {
      state.change_choose = payLoad.data
    },
    change_defaultChooses (state, payLoad) {
      state.defaultChooses = payLoad.defaultChooses
    },
    choose_data (state, payLoad) {
      var datas = state.choose_data
      if (payLoad.type === "color") {
        state.defaultChooses[0] = payLoad.index
        var pics = state.goodsDetail[0].pictures
        if (payLoad.index === "silver") {
          datas.choose_phone_pic = pics.silver
          datas.choose_color = "银色"
          datas.choose_color_index = "silver"
        } else if (payLoad.index === "deep_grey") {
          datas.choose_phone_pic = pics.deep_grey
          datas.choose_color = "深空灰色"
          datas.choose_color_index = "deep_grey"
        } else if (payLoad.index === "golden") {
          datas.choose_phone_pic = pics.golden
          datas.choose_color = "金色"
          datas.choose_color_index = "golden"
        } else {
          datas.choose_phone_pic = pics.rose_gold
          datas.choose_color = "玫瑰金色"
          datas.choose_color_index = "rose_gold"
        }
      } else {
        state.defaultChooses[1] = payLoad.index
        if (payLoad.index === "sixteen") {
          datas.choose_storage = "16GB"
          datas.choose_storage_index = "sixteen"
          datas.choose_price = 5288
        } else if (payLoad.index === "sixtyFour") {
          datas.choose_storage = "64GB"
          datas.choose_storage_index = "sixtyFour"
          datas.choose_price = 6088
        } else {
          datas.choose_storage = "128GB"
          datas.choose_storage_index = "one_hundred_and_twenty_eight"
          datas.choose_price = 6888
        }
      }
    }
  }
})

export default store



