<template>
  <div id="app">
    <ul class="nav">
      <router-link to="./" exact tag="li" event="click">
        <i class="iconfont icon-zhuye"></i>
        <span>home</span>
      </router-link>
      <router-link to="./goods" exact tag="li" event="click">
        <i class="iconfont icon-goods"></i>
        <span>goods</span>
      </router-link>
      <router-link to="./shoppingCart" tag="li" event="click">
        <i class="iconfont icon-gouwuche"></i>
        <span>shoppingCart</span>
        <span class="num" v-show="num>=1">{{num}}</span>
      </router-link>
    </ul>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
  computed: {
    num () {
      return this.$store.state.shoppingCartList.length
    }
  }
}
</script>

<style>
body{
  background: #f5f5f5;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>



