<template>
  <div class="shopping_cart_list clearFix">
    <div class="hint" v-show="listData.length===0">
      购物车空空如也
    </div>
    <div class="top_title" v-show="listData.length>=1">
      <input type="checkbox" id="check_all" @click="check_all" v-model="checked_state" />
      <label for="check_all" class="check_all_text">全选</label>
      <span class="commodity_name">商品名称</span>
      <span class="unit_price">单价</span>
      <span class="numbers">数量</span>
      <span class="subtotal">小计</span>
      <span class="operation">操作</span>
    </div>
    <ul class="list clearFix">
    	  <li v-for="item,index in listData" :class="{no_type: item.type===''}">
    	    <input type="checkbox" class="check_btn" v-model="item.checked" @click="choose(item.checked, index)" />
    	    <a href="" class="img_box">
    	      <img :src="item.picture" alt="" />
    	    </a>
    	    <span class="name_box">
    	      <span class="merchandise_name">
    	        <a href="" class="">{{item.name}} {{item.color}}</a>
    	      </span>
    	      <span class="type">存储容量：{{item.type}}</span>
    	    </span>
    	    <span class="unit_price">{{item.unit_price}}元</span>
    	    <span class="numbers">
    	      <num :datas="{index: index,num: item.pay_number}"></num>
    	    </span>
    	    <span class="subtotal">{{item.subtotal}}元</span>
        <span class="operation">
          <span class="del" title="删除" @click="del(index)">×</span>
          <span class="circular" title="删除"></span>
        </span>
    	  </li>
    </ul>
    <div class="statistics_box" v-show="listData.length>=1">
      <span class="all_number">
        共 <span class="num">{{listData.length}}</span> 件商品，已选择 <span class="num">{{check_num}}</span> 件
      </span>
      <button class="settlement" :disabled="check_num===0">去结算</button>
      <span class="total">合计：<span class="all_price">{{total}}</span>元</span>
    </div>
  </div>
</template>

<script>
/* eslint eqeqeq: 0 */
 import num from './numbers'
 var saveAndGet = {
   save (key, value) {
     localStorage.setItem(key, JSON.stringify(value))
   }
 }
 export default {
   name: 'shoppingCart',
   data () {
     return {
       checked_state: false
     }
   },
   watch: {
     listData: {
       handler () {
         this.check_function()
         saveAndGet.save('data', this.listData)
       },
       deep: true
     }
   },
   created () {
     this.check_function()
   },
   computed: {
     listData () {
       return this.$store.state.shoppingCartList
     },
     check_num () {
       var count = 0
       var datas = this.$store.state.shoppingCartList
       for (var i = 0; i < datas.length; i++) {
         if (datas[i].checked) {
           count += 1
         }
       }
       return count
     },
     total () {
       var count = 0
       var datas = this.$store.state.shoppingCartList
       for (var i = 0; i < datas.length; i++) {
         if (datas[i].checked) {
           count += datas[i].subtotal
         }
       }
       return count
     }
   },
   components: {
     num
   },
   methods: {
     check_all () {
       this.$store.commit('check_all', {
         check_state: this.checked_state
       })
     },
     choose (states, index) {
       this.$store.commit('choose', {
         index: index
       })
     },
     check_function () {
       var count = 0
       var datas = this.$store.state.shoppingCartList
       for (var i = 0; i < datas.length; i++) {
         if (datas[i].checked) {
           count += 1
         }
       }
       count == datas.length && count >= 1 ? this.checked_state = true : this.checked_state = false
     },
     del (index) {
       this.$store.commit('del', {
         index: index
       })
     }
   }
 }
</script>

<style>
</style>


