<template>
  <ul class="choose_list">
      <li v-for="item,index in data" :class="{active: choose_index === index}" @click="choose(index)">
        {{item}}
      </li>
  </ul>
</template>

<script>
export default {
  data () {
    return {
      choose_index: this.defaultChoose
    }
  },
  props: ['data', 'type', 'defaultChoose'],
  methods: {
    choose (index) {
      this.choose_index = index
      this.$store.commit('choose_data', {
        index: index,
        type: this.type
      })
    }
  }
}
</script>

<style>
</style>


