<template>
  <div class="goods">
    <div class="img_box">
      <img :src="choose_datas.choose_phone_pic" alt="" />
    </div>
    <div class="detail">
      <p class="title">{{datas.name}}</p>
      <p class="solid"></p>
      <p class="describe">
        <span class="type">描述：</span>
        <span class="describe_text">{{datas.describe}}</span>
      </p>
      <p class="price_box">
        <span class="type">价格：</span>
        <span class="price">￥
          <span v-show="choose_datas.choose_price === 0">{{datas.minPrice}} - {{datas.maxPrice}}</span>
          <span v-show="choose_datas.choose_price != 0">{{choose_datas.choose_price}}</span>
        </span>
      </p>
      <p class="appearance">
        <span class="type">外观：</span>
        <chooseList :data="datas.color" :type="types[0]" :defaultChoose="defaultChooses[0]"></chooseList>
      </p>
      <p class="storage">
        <span class="type">存储容量：</span>
        <chooseList :data="datas.storages" :type="types[1]" :defaultChoose="defaultChooses[1]"></chooseList>
      </p>
      <p class="solid"></p>
      <button
        class="add_shopping_cart"
        :disabled="choose_datas.choose_color === '' || choose_datas.choose_storage === ''"
        @click="add_shopping_cart">
        加入购物车
      </button>
    </div>
  </div>
</template>

<script>
/* eslint eqeqeq: 0 */
import chooseList from './chooseList'
export default {
  name: 'goods',
  data () {
    return {
      choose_datas: {},
      types: ['color', 'storage']
    }
  },
  created () {
    this.choose_datas = this.$store.state.choose_data
  },
  components: {
    chooseList
  },
  watch: {
    choose_datas: {
      handler () {
        this.$store.commit('change_choose', {
          data: this.choose_datas
        })
      },
      deep: true
    }
  },
  computed: {
    datas () {
      return this.$store.state.goodsDetail[0]
    },
    defaultChooses () {
      return this.$store.state.defaultChooses
    }
  },
  methods: {
    add_shopping_cart () {
      var datas = this.choose_datas
      this.$store.commit('add_shopping_cart', {
        pic: datas.choose_phone_pic,
        color: datas.choose_color,
        storage: datas.choose_storage,
        price: datas.choose_price,
        name: this.$store.state.goodsDetail[0].name
      })
    }
  }
}
</script>

<style>
</style>


